.highlight {
  position: absolute;
}

.emoji {
  position: absolute;
  color: black;
  opacity: 1;
  font-size: 28px;
}

.parts {
  opacity: 1;
}

.part {
  cursor: pointer;
  position: absolute;
  background: rgba(255, 226, 143, 1);
  transition: background 0.3s;
}

.scrolledTo .part {
  background: #ff4141;
}

.pdfhlWrapLabel {
  width: 100%;
  height: 100%;
  position: relative;
}

.pdfhlLabel {
  position: absolute;
  left: 0px;
  bottom: 100%;
  background: #ff3838;
  color: white;
  padding: 4px;
  font-size: 12px;
  line-height: 12px;
  border-radius: 4px;
}
